@keyframes spin {
  from {
    @include transform(rotate(0deg));
  }
  to {
    @include transform(rotate(360deg));
  }
}

@keyframes spin-reverse {
  from {
    @include transform(rotate(360deg));
  }
  to {
    @include transform(rotate(0deg));
  }
}

@keyframes opacity0To1 {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

