@keyframes height-animation-out {
  0% {
    height: 350%;
  }
  50% {
    height: 0%;
  }
  100% {
    height: 0%;
  }
}
@keyframes height-animation-in {
  0% {
    height: 0%;
  }
  50% {
    height: 350%;
  }
  100% {
    height: 350%;
  }
}
